import { Component, OnInit } from '@angular/core';
import { CoreRoutes, RoomsPageName } from "@core/configs/routing-config";
import { AnimationBuilder } from "@angular/animations";
import { AbstractAnimatedComponent } from "@shared/components/abstract-animated/abstract-animated.component";
import { DataService } from "@core/services/data.service";
import { IUser } from "@shared/models/user.model";
import { IRoomPage } from "@shared/models/room-page.model";

@Component({
  selector: 'app-introduction-page',
  templateUrl: './introduction-page.component.html',
  styleUrls: ['./introduction-page.component.scss']
})
export class IntroductionPageComponent extends AbstractAnimatedComponent implements OnInit {

  public roomsPageName = RoomsPageName;
  public coreRoutes = CoreRoutes;
  userData: IUser;
  pagesData: Array<IRoomPage>;

  constructor(private builder: AnimationBuilder, private _dataService: DataService) {
    super(builder);
  }

  ngOnInit() {
    this.userData = this._dataService.pagesData.user;
    this.pagesData = this._dataService.pagesData.pages;
  }
}
