import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeLayoutComponent } from "./home-layout/home-layout.component";
import { IntroductionPageComponent } from "./pages/introduction-page/introduction-page.component";
import { CompletedPageComponent } from "./pages/completed-page/completed-page.component";
import { CoreRoutes, LayoutRoutes } from "@core/configs/routing-config";
import { LinkNotValidComponent } from "./pages/link-not-valid/link-not-valid.component";
import { AnimationGuard } from "@core/guards/animation.guard";

const routes: Routes = [
  {
    path: LayoutRoutes.HOME,
    component: HomeLayoutComponent,
    canDeactivate: [AnimationGuard],
    children: [
      {
        path: CoreRoutes.INTRODUCTION,
        component: IntroductionPageComponent,
        canDeactivate: [AnimationGuard]
      },
      {
        path: CoreRoutes.COMPLETED,
        component: CompletedPageComponent,
        canDeactivate: [AnimationGuard]
      },
      {
        path: CoreRoutes.LINK_NOT_VALID,
        component: LinkNotValidComponent,
        canDeactivate: [AnimationGuard]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule {}
